
BODY {
  width: 100%;
  max-width: 100%;
}
.math {
  font-size: 4.5em;
  font-family: verdana;
  font-weight: bold;
  color: #00f;
  border: 2px solid blue;
  max-width: 300px;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
  margin-bottom: 8px;
  padding: 0px;
  line-height: .95em;
  padding-right: 30px;
  height: 3.3em;
}
.math DIV {
    text-align: right;
    margin-left: 30px;
}
.total {
  border-top: 10px solid blue;
  padding-top: 5px;
  margin-top: 12px;
}



