
.fa-icon {
  width: auto;
  height: 1em;
}
.del {
  color: #f66;
  font-size: 1em;
  padding-bottom: 0;
  line-height: .1em;
}
.eq-button {
  line-height: .8;
}
.eq {
  font-size: 1.2em;
  font-weight: bold;
}
.numpad {
  font-size: 4em;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 4.2em;
  margin-left: auto;
  margin-right: auto;
  border: 0px solid #ccc;
  border-radius: 6px;
}
.numpad DIV {
    width: 1em;
    border: 2px solid #ccc;
    border-radius: 10px;
    margin: 2px;
    padding: 8px;
    line-height: 1;
    height: .9em;
    cursor: pointer;
}



